import React, { Component } from 'react';
import classNames from 'classnames';
import * as Tool from 'tool';
import * as Common from 'tool/common';
import * as getUserIdInfo from 'tool/getUserId';
import { inject, observer } from 'mobx-react';

import styles from "./service-list.scss";
import Api from 'api';

import ErrorTips from './error-tips';
import GuideToast from './guide-toast';
import Loading from 'components/base/loading-inner';


@inject('Toast', 'ComNoContent', 'Header')
@observer
export default class BusinessDetail extends Component {
  constructor (props){
    super(props);
  }
  state = {
    query: Tool.getUrlParamQuery(),
    carInfo: {
			'carSpecId': '',
			'carSpecName': '',
			'carSeriesId': '',
			'carBrandId': '',
			'carBrandName': '',
			'carSeriesName': '请选择爱车'
    },
    isLoading: true,
    errorTipsText: '',
    errorTipsBtnText: '',
    openMileage: false,
    isLogin: true, // 是否登录
    isHasCar: true, // 车库是否有车
    maintain: [],
    mileagesArr: [],
    mileageTxt: '',
    guideIds: ['1', '2', '4', '3', '5', '6', '7', '8', '9', '10', '11', '12', '13' ],
    currentGuideId: '',
    guideToastShow: false,
    isBtnUnable: true, // 是否可以点击按钮
    isApp: Tool.webApp.isApp(),
    isAutoHomeApp: Tool.webApp.checkAutoHomeApp(), // 只在主软中
    isWz: Tool.webApp.checkViolatApp() || Tool.getCookie('iswz'),
    isAutoApp: Tool.webApp.checkMainApp() // 是否嵌入主软件 
  }

  componentDidMount() {
    this.props.Header.$setTitle('预约保养');
    Tool.setTitle('预约保养');
    // 设置订单入口pvareaid
    const { query } = this.state;
    Tool.setCookie('transparent_pvareaid', String(query.pvareaid).replace(/#\//, ''));
    if (query.isFaultMini){
      Tool.setCookie('isFaultMini', query.isFaultMini);
    }

    this.pvShow();
    setTimeout(() => {
      this.setAppHeader();
    }, 500);
    this.getMyCarFetch();
  }
  
  // pv统计
  pvShow = () => {
    let { userId } = getUserIdInfo.getUserId();
    Common.trackCustomPageInit({
      'pageId': 'tmfy_by_maintian_list_pv',
      'site_id': 1211120,
      'category_id': 1595,
      'sub_category_id': 18469
    }, {
      'userId': userId
    });
  }

  // 初始化接口
  initFetch = (fn = '') => {
    // 获取车系id
    let { carInfo, guideIds } = this.state;
    this.props.ComNoContent.$comConntent('');
    this.setState({
      isHasCar: true,
      isLoading: true
    });
    Api.getMaintainInit({
      specid: carInfo.carSpecId
    }).then(res => {
      if (res.returncode === 0) {
        let maintain = res.result.maintain.map((item) => ({
            ...item,
            isActive: false
        }));
        // 标识是否有厂商建议
        for (let i = 0; i < maintain.length; i++) {
          if (guideIds.indexOf(maintain[i].itemid) !== -1) {
            maintain[i].isHasGuide = true;
          } else {
            maintain[i].isHasGuide = false;
          }
        }
        this.setState({
          maintain: maintain,
          isLoading: false,
          mileagesArr: res.result.mileages
        }, () => {
          fn && fn();
        });
      }  else if (res.returncode === 50001) {
        // 清空数据
        this.setState({
          maintain: [],
          isLoading: false
        }, () => {
          // 无数据的情况
          this.props.ComNoContent.$comConntent('当前车型暂无推荐保养项目');
        });
      } else {
        return Promise.reject(res);
      }
    }).catch(error => {
      // 清空数据
      this.setState({
        maintain: [],
        isLoading: false,
        errorTipsText: '',
        errorTipsBtnText: '',
        openMileage: false,
        isLogin: true, // 是否登录
        isHasCar: true, // 车库是否有车
        mileagesArr: []
      });
      this.props.ComNoContent.$comConntent('页面溜走了，请稍候重试');
      console.log('getMaintainInit: error', error);
    });
  }

  // 获取缓存数据
  getCacheData = () => {
    let cacheData = {};
    cacheData.carInfo = JSON.parse(window.localStorage.getItem('carInfo') || '{}');
    cacheData.mileage = window.localStorage.getItem('mileage');
    return cacheData;
  }

  // 设置 app 头部背景图
  setAppHeader = () => {
    let { isAutoApp } = this.state;
    if (isAutoApp) {
      // eslint-disable-next-line
      require('jsBridge');
      window.jsBridgeV2.navigationcolorstyle({
        titleBgImageUrl: 'https://x.autoimg.cn/cfw/yc/cus/products/src/images/trans-title-1.png',
        titlecolor: '#ffffff',
        closeImageStyle: 0,
        statusBarStyle: 0
      });
    }
  }
  // 获取车库车辆
  getMyCarFetch = () => {
    // 优先判断缓存是否有车
    let getCacheData = this.getCacheData();
    if (getCacheData.carInfo && getCacheData.carInfo.carSpecId) {
      let mileage = getCacheData.mileage;
      this.setState({
        carInfo: getCacheData.carInfo,
        mileageTxt: mileage
      }, () => {
        this.initFetch(() => {
          this.getMaintainAdviceFetch({
            mileage: mileage,
            specid: getCacheData.carInfo.carSpecId
          });
        });
      });
    } else {
      Api.getMyCarList({}).then((res) => { // 获取用户车库列表
        if (res.returncode === 0) {
          // 设置登录状态
          this.setState({
            isLogin: true
          });
          if (res.result.carInfo && res.result.carInfo.length) {
            this.setState({
              carInfo: res.result.carInfo[0]
            }, () => {
              // 缓存当前车辆
              this.cacheCarInfo();
              this.initFetch();
            });
          } else {
            // 无车情况
            this.setState({
              isHasCar: false,
              errorTipsText: '添加您的爱车，享受保养方案推荐服务',
              errorTipsBtnText: '添加爱车'
            });
            this._handleAddCAR();
          }
        } else if (res.returncode === 501) {
          this.setState({
            isLogin: false,
            errorTipsText: '登录查看，便于针对车型精准适配',
            errorTipsBtnText: '登录'
          }, () => {
            this.handleAppLogin();
          });
        } else {
          return Promise.reject(res);
        }
      }).catch((err) => {
        console.log('获取车辆失败');
        console.log(err);
      });
    }
  }

  // 缓存车辆信息
  cacheCarInfo = () => {
    let { carInfo } = this.state;
    window.localStorage.setItem('carInfo', JSON.stringify(carInfo));
    window.localStorage.setItem('mileage', '');
  }

  // 添加爱车方法
  _handleAddCAR = () => {
    let carInfo = {};
    Common.addCar({
      success: (data) => {
        let { isAutoHomeApp } = this.state;
        if (isAutoHomeApp) {
          let { brandid, brandname, seriesid, seriesname, specid, specname } = data.carInfo;
          carInfo = {
            carSpecId: specid,
            carSpecName: specname,
            carSeriesId: seriesid,
            carBrandId: brandid,
            carBrandName: brandname,
            carSeriesName: seriesname
          };
        }

        this.setState({
          carInfo: carInfo
        }, () => {
          // 缓存当前车辆
          this.cacheCarInfo();
          this.initFetch();
        });
      }
    });
  }

  // 唤起客户端登陆
  handleAppLogin = () => {
    // 调用登录
    let backUrl = encodeURIComponent(window.location.href);
    Common.handleLogin({
      backUrl: backUrl,
      queryParams: {},
      successCb: (res) => {
        console.log(res, '登录成功回调');
        this.getMyCarFetch();
      },
      failCb: (err) => {
        console.log(err, '登录失败回调');
      }
    });
  }
  toggleOpenMileage = () => {
    let { maintain } = this.state;
    if (!maintain.length) {
      return;
    }
    this.setState({
      openMileage: !this.state.openMileage
    });
  }
  // 点击里程
  handleMileage = (mileage) => {
    let { carInfo } = this.state;
    // 隐藏弹窗
    this.setState({
      openMileage: false,
      mileageTxt: mileage
    });
    this.handleTrackCustomEvent('tmfy_by_maintain_list_choose_mileage_click', {
      objectId: mileage
    });
    // 缓存用户选择里程
    window.localStorage.setItem('mileage', mileage);
    this.getMaintainAdviceFetch({
      mileage: mileage,
      specid: carInfo.carSpecId
    });
  }

  // 获取高亮保养项接口数据
  getMaintainAdviceFetch = ({ mileage = '', specid = '' }) => {
    Api.getMaintainAdvice({
      mileage: mileage,
      specid: specid
    }).then((res) => { // 获取用户车库列表
      if (res.returncode === 0) {
        let activeData = res.result;
        let isActiveArr = activeData.filter(item => item.state);
        if (isActiveArr.length) { // 可以点击按钮
          this.setState({
            isBtnUnable: false
          });
        }
        // 根据接口返回变更默认高亮
        let { maintain } = this.state;
        let newMaintain = maintain.map((item, index) => (
          {
            ...item,
            isActive: activeData[index].state
          }
        ));
        this.setState({
          maintain: newMaintain
        });
      } else {
        return Promise.reject(res);
      }
    }).catch((err) => {
      console.log('getMaintainAdvice:error', err);
    });
  }

  // 选择车辆
  handleAddCar = () => {
    let carInfo = {};
    this.setState({
      openMileage: false
    });
    Common.choseCar({
      success: (res) => { // 选择车辆成功回调
        if (Tool.webApp.checkMainApp()) { // 主软和点评
          let { brandId, brandName, seriesId, seriesName, specId, specName } = res.result.data;
          carInfo.carSpecId = specId;
          carInfo.carSpecName = specName;
          carInfo.carSeriesId = seriesId;
          carInfo.carBrandId = brandId;
          carInfo.carBrandName = brandName;
          carInfo.carSeriesName = seriesName;
        } else if (Tool.webApp.checkViolatApp()) { // 违章
          if (res.code === 0) {
            if (res.extras.type === 14) {
              let { brandid, brandname, seriesid, seriesname, specid, specname } = res.extras;
              carInfo.carSpecId = specid;
              carInfo.carSpecName = specname;
              carInfo.carSeriesId = seriesid;
              carInfo.carBrandId = brandid;
              carInfo.carBrandName = brandname;
              carInfo.carSeriesName = seriesname;
            }
          }
        }
        this.handleTrackCustomEvent('tmfy_by_maintain_list_choose_car_click', {
          objectId: `${carInfo.carBrandId}-${carInfo.carSpecId}-${carInfo.carSeriesId}`
        });
        this.setState({
          carInfo: carInfo,
          mileageTxt: '',
          isBtnUnable: true
        }, () => {
          // 缓存当前车辆信息
          this.cacheCarInfo();
          this.initFetch();
        });
      },
      fail: (err) => { // 添加车辆失败回调
        console.log(err, 'choseCar:error');
      }
    });
  }
  // 点击当前保养项目
  handleMaintainList = (event, data) => {
    event.stopPropagation();
    event.cancelBubble = true;
    // 循环更新选择状态
    let { maintain, mileageTxt } = this.state;
    if (!mileageTxt) {
      this.props.Toast.$toast('请选择公里数', 2000);
      return;
    }
    for (let i = 0 ; i < maintain.length; i++) {
      if (data.itemid === maintain[i].itemid) {
        maintain[i].isActive = !data.isActive;
      }
    }
    let isActiveArr = maintain.filter(item => item.isActive);
    if (!isActiveArr.length) {
      this.setState({ // 按钮不可点击
        isBtnUnable: true
      });
    } else {
      this.setState({ // 按钮可点击
        isBtnUnable: false
      });
    }
    this.setState({
      maintain: maintain
    });
  }
  // 关闭选择公里数
  handleClostMileage = (event) => {
    event.stopPropagation();
    event.cancelBubble = true;
    this.setState({
      openMileage: false
    });
  }
  // 点击防坑指南
  handleGuide = (event, data) => {
    event.stopPropagation();
    event.cancelBubble = true;
    this.handleTrackCustomEvent('tmfy_by_maintain_list_fkzn_click');
    this.setState({
      guideToastShow: true,
      currentGuideId: data.itemid
    });
  }
  // 关闭防坑指南
  handleCloseToast = () => {
    this.setState({
      guideToastShow: false
    });
  }
  // 点击预约
  handleOrderBtn = () => {
    let { isBtnUnable, maintain, carInfo, mileageTxt } = this.state;
    if (isBtnUnable) { // true 时候不可以点击按钮
      return;
    } 
    this.handleTrackCustomEvent('tmfy_by_maintain_list_book_click');
    // 获取选择项目id
    let itemIds = maintain.filter(item => item.isActive).map((item, index) => (item.itemid)).join(',');
    Common.pageJumpHandel('dealerList', {
      type: 0,
      itemIds: itemIds, // 选择的项目id
      plateNumber: carInfo.carNum, // 车牌号
      brandId: carInfo.carBrandId, // 品牌id
      brandName: encodeURIComponent(encodeURIComponent(carInfo.carBrandName)), // 品牌名称
      seriesId: carInfo.carSeriesId, // 车系id
      seriesName: encodeURIComponent(encodeURIComponent(carInfo.carSeriesName)), // 车系名称
      specId: carInfo.carSpecId, // 车型id
      specName: encodeURIComponent(encodeURIComponent(carInfo.carSpecName)), // 车型名称
      travelDistance: mileageTxt // 里程值（数值）
    });
  }
  // 点击保养手册
  handleIconMaintain = () => {
    let { carInfo } = this.state;
    this.handleTrackCustomEvent('tmfy_by_maintain_list_bysc_click');
    let path = `${Common.getRemarkHostUrl()}/carcomment/cartreasure?isFromSource=fault&tabNavValue=1&carSeriesId=${carInfo.carSeriesId || ''}&carSpecId=${carInfo.carSpecId || ''}&carBrandId=${carInfo.carBrandId || ''}`;
    Common.pageJumpHandel(path);
  }
  // 接受错误提示按钮回调
  handleErrorTipsChange = (data) => {
    if (data.indexOf('登录') !== -1) {
      this.handleAppLogin();
    } else if (data.indexOf('添加爱车') !== -1) {
      this._handleAddCAR();
    }
  }

  // 点击统计事件
  handleTrackCustomEvent(action, { objectId = '' } = {}) {
    let { userId } = getUserIdInfo.getUserId();
    // eslint-disable-next-line
    trackCustomEvent('yc_page_common_click', {
      'bus': '1',
      'refpage': Tool.getCookie('refpage') || '',
      'channel': Tool.getCookie('channel'),
      'city': Tool.getCookie('cityId') || 110100,
      'action': action
    }, {
      'userId': userId,
      'objectId': objectId
    });
  }

  render() {
    const {
      maintain,
      mileagesArr,
      openMileage,
      isApp,
      isBtnUnable,
      mileageTxt,
      guideToastShow,
      currentGuideId,
      carInfo,
      isLogin,
      isHasCar,
      errorTipsText,
      errorTipsBtnText,
      isLoading
    } = this.state;
    return (
      <div className={classNames({[styles.servicelist_view]: true, [styles.m_t]: !isApp})}>
        {
          isLogin ? (
            isHasCar ? (
              <>
                <div className={styles.car_item}>
                  <div className={styles.car} onClick={this.handleAddCar}>
                    <p>{carInfo.carSeriesName}&nbsp;{carInfo.carSpecName}</p> <i className={styles.icon_arrow_r}></i>
                  </div>
                  <i className={styles.line}></i>
                  <p className={styles.mileage} onClick={this.toggleOpenMileage}>
                    {
                      mileageTxt ? `${mileageTxt}km` : '请选择当前公里数'
                    }
                    <i className={classNames({[styles.icon_arrow_down]: true, [styles.icon_arrow_r]: true, [styles.icon_arrow_up]: openMileage})}></i>
                  </p>
                </div>
                <div className={styles.list_cont}>
                  <ul className={styles.list} ref='listDOM'>
                    {
                      maintain.length > 0 && (
                        maintain.map((item, index) => (
                          <li key={index} onClick={(e) => this.handleMaintainList(e, item)}>
                            <div className={styles.cell_hd}>
                              <i className={classNames({[styles.icon_circle]: true, [styles.icon_circle_active]: item.isActive})}></i>
                              <span className={styles.name}>
                                {item.itemname}
                              </span>
                              {
                                item.isHasGuide && (
                                  <span className={styles.tips_btn} onClick={(e) => this.handleGuide(e, item)}>防坑指南<i className={styles.icon_arrow_blue}></i></span>
                                )
                              }
                            </div>
                            
                            <div className={styles.cell_bd}>
                              <p className={styles.txt}>{item.content}</p>
                              {
                                'itemtotalprice' in item && item.itemtotalprice !== '-' && (
                                  <span className={styles.price}><i>参考价格</i>￥{item.itemtotalprice}<i>起</i></span>
                                )
                              }
                            </div> 
                          </li>
                        ))
                      )
                    }
                  </ul>
                </div>
                {
                  maintain.length > 0 && (
                    <div className={classNames({[styles.btn_item]: true, [styles.unable]: isBtnUnable})}>
                      <span className={styles.maintain_item} onClick={this.handleIconMaintain}>
                        <i className={styles.icon_maintain}></i>
                        保养手册
                      </span>
                      <span className={styles.btn_cell} onClick={this.handleOrderBtn}>
                        立即免费预约
                      </span>
                    </div>
                  )
                }
                {/* 里程选择 */}
                { openMileage ? (
                    <div className={classNames(styles.mileage_toast)} onClick={(e) => this.handleClostMileage(e)}>
                      <div className={styles.mileage_cont}>
                        <ul>
                          {
                            mileagesArr.length > 0 && mileagesArr.map((item, index) => (
                              <li key={index} onClick={() => this.handleMileage(item)}>{item}km</li>
                            ))
                          }
                        </ul>
                      </div>
                    </div>
                  ) : null }
                {/* 里程选择 */}
                {/* 厂商建议弹窗 */}
                <GuideToast showToast={guideToastShow} closeToast={this.handleCloseToast} guideId={Number(currentGuideId)}></GuideToast>
                {/* /end  厂商建议弹窗*/}
                <Loading show={ isLoading } />
              </>
            ) : <ErrorTips content={errorTipsText} btnText={errorTipsBtnText} changeBtn={this.handleErrorTipsChange}></ErrorTips>
          ) : <ErrorTips content={errorTipsText} btnText={errorTipsBtnText} changeBtn={this.handleErrorTipsChange}></ErrorTips>
        }
      </div>
    );
  }
}