import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classNames from 'classnames';

import styles from './error-tips.scss';

export default class ErrorTips extends Component {

  static propTypes = {
    content: PropTypes.string,
    btnText: PropTypes.string,
    changeBtn: PropTypes.func
  }

  constructor (props) {
    super(props);
  }
  state = {
    content: this.props.content || '',
    btnText: this.props.btnText || ''
  }

  handleBtn = () => {
    let { btnText } = this.state;
    this.props.changeBtn && this.props.changeBtn(btnText);
  }

  render () {
    let { content, btnText } = this.state;
    return (
      <>
        {
          content ? (
            <div className={styles.error_tips}>
              <div className={styles.nocontent_icon}></div>
              <p>{ content }</p>
              {
                btnText ? (
                  <div className={ styles.reload_btn } onClick={this.handleBtn}>
                    { btnText }
                  </div>
                ) : null
              }
            </div>
          ) : null
        }
      </>
    );
  }
}
